.home-page-slide {
  background-image: url("../../images/home-page-slide-1.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 70vh;
  z-index: 1;
  position: relative;
}
.home-page-slide article {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  position: relative;
  text-align: center;
}
.home-page-slide article::before {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(170, 170, 170, 0.231372549);
  content: "";
  top: 0;
  z-index: -1;
}
.home-page-slide article h1 {
  color: #322b2b;
  line-height: 2;
}
.home-page-slide article .offer {
  color: #1e1a1a;
  font-size: 20px;
  font-weight: 900;
}
.home-page-slide article form {
  width: 50%;
}
.home-page-slide article form input {
  width: 100%;
  padding: 10px;
  outline: none;
}
.home-page-slide article form .subscribe {
  top: 0;
  right: -23px;
  line-height: 2.5;
  outline: none;
  border: none;
  background: #3bb77e;
  cursor: pointer;
  color: #fff;
}/*# sourceMappingURL=home-page-slider.css.map */